body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: auto;
  table-layout: fixed;

  position: absolute;
}
.board{
  width:1000px;
}
.status {
  margin-bottom: 10px;
  font-size:50px;
  text-align:center;
  color: #007492;
}
.substatus {
  margin-bottom: 10px;
  margin-top:20px;
  font-size:20px;
  text-align:center;
  color: #007492;
}
.boardrow{
  position:absolute;
  table-layout: fixed;
}
.square {
  background: #fff;
  background-size: cover;
  background-position: center;
  border: 2px solid #007492;
  border-collapse: collapse;
  height: 100px;
  width: 200px;
  overflow: hidden;

}

.square h2{
  margin-bottom: 10px;
  font-size:200%;
  color: #FFF;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-info {
  margin-left: 20px;
}

.NewGameButton{
  margin-left: auto;
  margin-right:auto;
  width:200px;
  align:center;
}






